import Vue from 'vue'
import Router from 'vue-router'
import Main from '@/components/Main'
import Vuetify from 'vuetify'
import 'vuetify/dist/vuetify.min.css'
import myWorld from "@/components/world/myWorld";
import news from "@/components/world/news";
import essay from "@/components/world/essay";

Vue.use(Vuetify)

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Main',
      component: Main
    },
    {
      path:'/myWorld',
      name:'myWorld',
      component:myWorld,
      children:[
        {
          path:'/news',
          name:'news',
          component:news,
        },
        {
          path:'/essay',
          name:'essay',
          component:essay,
        }
      ]
    }
  ]
})



