<template>
  <v-app light :style="{height: screenHeight + 'px', overflowY: 'hidden'}">
    <v-navigation-drawer app v-model="drawer" fixed style="background-color:rgba(255,240,245,0.8)">
      <div class="drawer-header">
        <v-avatar>
          <img src="../../assets/avatar.png" alt="avatar">
        </v-avatar>
        <p>等风来</p>
      </div>
      <v-list class="pt-0" dense style="background-color: #ffffff00; color:#e65100;">
        <v-divider></v-divider>
        <v-list-tile v-for="(item,index) in items" :key="item.title" @click="jump(index)">
          <v-list-tile-action>
            <v-icon style="color:#e65100">{{ item.icon }}</v-icon>
          </v-list-tile-action>
          <v-list-tile-content>
            <v-list-tile-title>{{ item.title }}</v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>
      </v-list>
    </v-navigation-drawer>
    <v-toolbar app fixed color="rgba(206,147,216,0.8)">
      <v-avatar @click.stop="drawer=!drawer">
        <img src="../../assets/avatar.png" alt="avatar">
      </v-avatar>
      <v-toolbar-title class="white--text">等风也等你</v-toolbar-title>
      <v-spacer></v-spacer>
      <v-btn @click="toMain" icon>
        <v-icon style="color: white">home</v-icon>
      </v-btn>
    </v-toolbar>
    <v-content :class="{'main':isPC,'main-phone':!isPC}">
      <router-view></router-view>
    </v-content>
  </v-app>
</template>

<script>
    export default {
      name: "myWorld",
      data:()=>{
        return {
          screenHeight: 0,
          isPC:true,
          drawer:true,
          index:1,
          items:[
            { title: '最新动态', icon: 'book' },
            { title: '踩坑日志', icon: 'work' },
            { title: '娱乐日志', icon: 'radio' },
            { title: '等风来的随笔', icon: 'assignment' }
          ]
        }
      },
      mounted() {
        this.screenHeight = window.innerHeight;
        this.isPC = window.innerWidth >=1000;
        this.drawer = this.isPC;
        window.onresize = () => {
          this.isPC = window.innerWidth >=1000;
          this.screenHeight = window.innerHeight;
        };
        this.$router.push("/news");
      },
      methods:{
        jump(index){
          if(!this.isPC){
            this.drawer=false;
          }
          switch (index) {
            case 0:{
              this.$router.push({
                path:'/news',
                name:'news'
              })
              break;
            }
            case 1:{
              alert('暂未开放～')
              break;
            }
            case 2:{
              alert('暂未开放～')
              break;
            }
            case 3:{
              this.$router.push({
                path:'/essay',
                name:'essay'
              })
              break;
            }
          }
        },
        toMain(){
          this.$router.push({
            path:'/',
            name:'Main'
          })
        }
      }
    }
</script>

<style scoped>
  .drawer-header{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color:rgba(255,167,38,0.8);
    color:white;
    height: 20vh;
  }
  .drawer-header p{
    margin-top: 13px;
    font-size: 20px;
  }
  .main{
    height: 100%;
    background-image: url("../../assets/dieqi_world.jpg");
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    background-attachment: fixed;
    overflow-y: auto;
  }
  .main-phone{
    height: 100%;
    background-image: url("../../assets/daozhang.jpg");
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    background-attachment: fixed;
    overflow-y: auto;
  }
</style>



