<template>
  <v-container class="container">
    <v-card color="rgba(255,240,245,.5)">
      <v-toolbar dark color="rgba(106,27,154,.8)">
        <v-toolbar-title>最新动态</v-toolbar-title>
      </v-toolbar>
      <v-list style="background-color:rgba(255,240,245,.5)">
        <v-list-tile v-for="item in news"
          :key="item.title"
          @click="">

          <v-list-tile-content>
            <v-list-tile-title v-text="item.title"></v-list-tile-title>
          </v-list-tile-content>

          <v-list-tile-action>
            <v-chip color="#ff9800" text-color="white">{{item.time}}</v-chip>
          </v-list-tile-action>
        </v-list-tile>
      </v-list>
    </v-card>
  </v-container>
</template>

<script>
  export default {
    name: "news",
    data: () => {
      return {
        news: [
          { "title": "更新了动态", "time": "2019-6-1" },
          { "title": "开始学习node", "time": "2019-6-2" },
          { "title": "开始学习vue", "time": "2019-6-3" },
          { "title": "继续为了生活奋斗", "time": "2019-6-5" },
          { "title": "更新了娱乐日志", "time": "2019-6-6" },
        ]
      }
    }
  }
</script>

<style scoped>
  .container{
    margin-top: 8vh;
  }

</style>



