<template>
  <v-container class="container">
    <v-card color="rgba(255,240,245,.5)">
      <v-toolbar dark color="rgba(106,27,154,.8)">
        <v-toolbar-title>随笔</v-toolbar-title>
      </v-toolbar>
      <v-list style="background-color:rgba(255,240,245,.5)">
        <v-list-tile v-for="item in news"
                     :key="item.title"
                     @click="toDetail">

          <v-list-tile-content>
            <v-list-tile-title v-text="item.title"></v-list-tile-title>
          </v-list-tile-content>

          <v-list-tile-action>
            <v-chip color="#ff9800" text-color="white">{{item.time}}</v-chip>
          </v-list-tile-action>
        </v-list-tile>
      </v-list>
    </v-card>
  </v-container>
</template>

<script>
    export default {
      name: "essay",
      data: () => {
        return {
          news: [
            { "title": "且行且珍惜", "time": "2019-6-1" },
            { "title": "等风来", "time": "2019-6-15" }
          ]
        }
      },
      methods:{
        toDetail(){

        },
      }
    }
</script>

<style scoped>
  .container{
    margin-top: 8vh;
  }
</style>



