<script>
    export default {
      name: "Main",
      data: () => {
        return {
          screenHeight: 0,
          isPC: true
        }
      },
      mounted() {
        this.screenHeight = window.innerHeight;
        this.isPC = window.innerWidth >= 1000;
        window.onresize = () => {
          this.isPC = window.innerWidth >= 1000;
          this.screenHeight = window.innerHeight;
        }
      },
      methods:{
        toWorld(){
          this.$router.push({
            path:'/myWorld',
            name:'myWorld'
          })
        }
      }
    }
</script>

